	module.exports = {"data":{"prismicDocument":{"data":{"feature_name":[{"type":"heading1","text":"Reporting"}],"feature_tagline":[{"type":"heading2","text":"Report on every aspect of your business and your care provision"}],"sell":[{"type":"paragraph","text":"Report on any aspect of your business, and email, text, print or export the results. Import and export your data"}],"feature_icon":{"url":"https://prismic-io.s3.amazonaws.com/careplanner%2Fa1b61faa-51ea-4967-92bd-7363e29c1c5b_excel-seeklogo.com.svg"},"feature_image":{"url":"https://prismic-io.s3.amazonaws.com/careplanner/567d4ddaafc720acffef173fe074c691a971f44f_reports-image-2.png"},"feature_body_copy":[{"type":"paragraph","text":"It's all very well being able to put information into a system, but that's no good if you can't get it out again in the format you need."},{"type":"paragraph","text":"Fortunately, CarePlanner makes it easy to generate extremely detailed reports on every aspect of your business, and then to print, email or export these reports at the click of a button."},{"type":"heading2","text":"Training and Compliance Reports"},{"type":"paragraph","text":"Let's say you're facing a CQC inspection, and need to be able to show that all your staff are up-to-date with their training. One simple report can take care of this. Similarly, lists of all required documentation for both service users and staffs can be pulled up in one printable report."},{"type":"heading2","text":"Holiday Reporting"},{"type":"paragraph","text":"If the summer is looming and you want to look at holiday bookings and entitlement, we've got a report that can show you what you need to know. And similarly, if you want to know how many of your clients don't require care at the moment, then CarePlanner can tell you."},{"type":"heading2","text":"Timekeeping and Call Monitoring Reports"},{"type":"paragraph","text":"If you are interested in care quality and punctuality statistics, you can draw on the data stored by one of our call monitoring systems. Again, this is all available in one sortable, filterable report."},{"type":"heading2","text":"Contact Details Reports"},{"type":"paragraph","text":"Sometimes, reporting requirements are simple – like printing off a list of staff contact details, perhaps for an on-call supervisor who won't have access to the main CarePlanner system. Fortunately, reports like this are just one click away, and can be customised to show just the data that is necessary and required."},{"type":"heading2","text":"Custom Reporting"},{"type":"paragraph","text":"Finally, you might already have a suite of business KPIs and metrics that you want to run on the raw appointment data within CarePlanner. That's okay - we've made it simple for you to pull out literally every detail possible for each call that your care staff have made within any date range."},{"type":"paragraph","text":"We believe that there's something for everyone in our reporting system – from super-quick rota generation for admin staff, through to comprehensive financial reports for management and accountants."}],"feature_bullets":[{"feature_bullet":[{"type":"paragraph","text":"Generate reports on everything from staff timekeeping to outstanding invoices"}]},{"feature_bullet":[{"type":"paragraph","text":"Reports can be emailed, texted, printed or exported to spreadsheets"}]},{"feature_bullet":[{"type":"paragraph","text":"Save customised or filtered versions of reports"}]},{"feature_bullet":[{"type":"paragraph","text":"Import CarePlanner data into other software packages"}]}],"features_sidebar":[{"feature_text":[]}]}}},"pathContext":{"permalink":"/features/reporting","prev":{"slugs":["travel"],"type":"feature","fields":{"permalink":"/features/travel"},"data":{"feature_name":[{"type":"heading1","text":"Travel"}],"feature_tagline":[{"type":"heading2","text":"Automated mileage calculations, GPS tracking and rota highlighting"}],"sell":[{"type":"paragraph","text":"Get automated mileage calculations, GPS navigation, buffer times between calls and rota highlights when you've allowed too little time"}],"title":null,"excerpt":null,"feature_image":{"url":null},"key_stat_figure":null,"key_stat_text":null,"large_image":null,"summary":null,"company_name":null}},"next":{"slugs":["travel"],"type":"feature","fields":{"permalink":"/features/travel"},"data":{"feature_name":[{"type":"heading1","text":"Travel"}],"feature_tagline":[{"type":"heading2","text":"Automated mileage calculations, GPS tracking and rota highlighting"}],"sell":[{"type":"paragraph","text":"Get automated mileage calculations, GPS navigation, buffer times between calls and rota highlights when you've allowed too little time"}],"title":null,"excerpt":null,"feature_image":{"url":null},"key_stat_figure":null,"key_stat_text":null,"large_image":null,"summary":null,"company_name":null}}}}

